<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>test</h2>

  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: 'hi 2.0'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

</style>



